import React, { useState } from 'react';
import { graphql } from 'gatsby';
import SwipeableViews from 'react-swipeable-views';

import ProjectMedia from '../components/ProjectMedia';
import Tooltip from '../components/Tooltip';

import { ReactComponent as ProjectIcon } from '../../static/code.svg';
import { ReactComponent as GithubIcon } from '../../static/github.svg';
import { ReactComponent as LinkIcon } from '../../static/external-link.svg';
import { ReactComponent as LeftIcon } from '../../static/chevron-left.svg';
import { ReactComponent as RightIcon } from '../../static/chevron-right.svg';

export default function Projects({ data: { projects: { edges: projects } } }) {
    const [projectIdx, setProjectIdx] = useState(0);
    const [projectLayoutMode, setProjectLayoutMode] = useState('linear');

    return (
        <main className="page">
            <div className="header">
                <div className="icon-wrapper icon-wrapper--primary icon-wrapper--space-r">
                    <div className="icon">
                        <ProjectIcon />
                    </div>
                </div>
                <h1 className="heading heading--xl heading--primary">Projects</h1>
            </div>
            <div className={`projects-body projects-body--${projectLayoutMode}`}>
                {
                    projectLayoutMode === 'linear'
                    ?
                    (
                        <>
                            <div className="projects-body__project-swipeable">
                                <SwipeableViews
                                className="projects-body__project-info"
                                index={projectIdx}
                                onChangeIndex={(idx) => {
                                    if (projectIdx !== idx) { 
                                        setProjectIdx(idx);
                                    }
                                }}>
                                {
                                    projects.map(({ node: { frontmatter: { title, external, github, stack }, html: bodyContent }}) => (
                                        <div className="projects-body__project-info-content">
                                            <div className="header projects-body__project-header">
                                                <h2 className="heading heading--lg">{title}</h2>
                                            </div>
                                            <div className="projects-body__body-content" dangerouslySetInnerHTML={{__html: bodyContent}}></div>
                                            <div className="projects-body__stack-info">
                                                <span className="projects-body__emphasis-text">Built with: </span>
                                                <ul className="list list--horizontal projects-body__stack-list">
                                                {
                                                    stack.map((tech) => (
                                                        <li className="projects-body__stack-item">{tech}</li>
                                                    ))
                                                }
                                                </ul>
                                            </div>
                                            <div className="projects-body__project-links">
                                                <span className="projects-body__emphasis-text">Links: </span>
                                            {
                                                github && <a className="projects-body__project-link" href={github} target="_blank" rel="noreferrer"><GithubIcon className="icon icon--small projects-body__project-link-icon" /></a>
                                            }
                                            {
                                                external && <a className="projects-body__project-link" href={external} target="_blank" rel="noreferrer"><LinkIcon className="icon icon--small projects-body__project-link-icon" /></a>
                                            }
                                            </div>
                                        </div>
                                    ))
                            
                                }
                                </SwipeableViews>
                                <div className="projects-body__slide-controls">
                                    <div className="projects-body__slide-control">
                                        <button 
                                        className="projects-body__slide-control-button"
                                        aria-describedby="left-control-title"
                                        onClick={(e) => projectIdx !== 0 && setProjectIdx(projectIdx - 1)}>
                                            <span className="projects-body__slide-control-name projects-body__slide-control-name--left">Prev Project</span>
                                            <div className="icon-wrapper icon-wrapper--transition projects-body__slide-control-icon-wrapper">
                                                <LeftIcon />
                                            </div>
                                        </button>
                                        {projectIdx !== 0 ? <Tooltip id="left-control-title" position="bottom" align="right">{projects[projectIdx - 1].node.frontmatter.title}</Tooltip> : <span>&nbsp;</span>}
                                    </div>
                                    <div className="projects-body__slide-control">
                                        <button 
                                        className="projects-body__slide-control-button" 
                                        aria-labelledby="right-control-label" 
                                        aria-describedby="right-control-title"
                                        onClick={(e) => projectIdx !== projects.length - 1 && setProjectIdx(projectIdx + 1)}>
                                            <div className="icon-wrapper icon-wrapper--transition projects-body__slide-control-icon-wrapper">
                                                <RightIcon />
                                            </div>
                                            <span className="projects-body__slide-control-name">Next Project</span>
                                        </button>
                                        
                                        {projectIdx !== projects.length - 1 && <Tooltip id="right-control-title" position="bottom">{projects[projectIdx + 1].node.frontmatter.title}</Tooltip>}
                                    </div>
                                </div>
                            </div>
                            <ProjectMedia />
                        </>
                    )
                    :
                    (
                        <></>
                    )
                }

            </div>
        </main>
    )
}

export const projectQuery = graphql`
query {
    projects: allMarkdownRemark {
      edges {
        node {
          frontmatter {
            external
            github
            title
            stack
          }
          html
        }
      }
    }
  }
`;